@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: radial-gradient(
    circle,
    rgba(31, 55, 86, 1) 0%,
    rgba(20, 21, 57, 1) 100%
  );
  font-family: 'Barlow Semi Condensed', sans-serif;

  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.score-main {
  width: 150px;
  height: 115px;
  background-color: white;
  margin-left: 25px;
  margin-right: 25px;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 7px;
}

.score-details {
  padding: 18px;
  margin: auto;
  text-align: center;
}
.score-text {
  color: #2a46c0;
  font-size: 20px;
  font-weight: 600;
}
.score-number {
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  color: #3b4363;
}

@media only screen and (max-width: 500px) {
  .score-main {
    width: 80px;
    height: 70px;
  }
  .score-details {
    padding: 10px;
    margin: auto;
    text-align: center;
  }
  .score-text {
    color: #2a46c0;
    font-size: 10px;
    font-weight: 600;
  }
  .score-number {
    font-size: 40px;
    font-weight: 700;
    line-height: 40px;
    color: #3b4363;
  }
}

.head-container {
  padding: 30px;
  z-index: 1;
}
.head-main {
  width: 700px;
  height: 150px;
  margin: auto;

  border-style: solid;
  border-width: 3px;
  border-radius: 7px;
  border-color: #606e85;
  display: flex;
  justify-content: space-between;
}

.head-main img {
  padding: 30px;
}

@media only screen and (max-width: 780px) {
  .head-main {
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
  .head-main {
    height: 100px;
  }
  .head-main img {
    padding: 25px;
    width: 100px;
    height: 60px;
  }
}

.paper {
  background: linear-gradient(
    90deg,
    rgba(72, 101, 244, 1) 0%,
    rgba(86, 113, 245, 1) 100%
  );
  box-shadow: 0 9px rgb(42, 59, 146);
  width: 200px;
  height: 200px;
  border-radius: 100%;
  margin: auto;
  display: grid;
  place-items: center;
  animation-direction: alternate;
}

.paper-center {
  background: white;
  width: 150px;
  height: 150px;
  border-radius: 100%;

  display: grid;
  place-items: center;
}

.paper-center img {
  width: 60px;
}

@keyframes pulsatePaper {
  0% {
    box-shadow: 0 9px rgb(42, 59, 146), 0 0 5px 20px #26387098,
      0 0 5px 40px #26387073, 0 0 5px 60px #26387042;
  }
  100% {
    box-shadow: 0 9px rgb(42, 59, 146), 0 0 5px 60px #26387098,
      0 0 5px 120px #26387073, 0 0 5px 180px #26387042;
  }
}

@media only screen and (max-width: 500px) {
  .paper {
    width: 100px;
    height: 100px;
  }
  .paper-center {
    width: 75%;
    height: 75%;

    display: grid;
    place-items: center;
  }
  .paper-center img {
    width: 30px;
  }
}

.scissors {
  background: linear-gradient(
    90deg,
    rgba(236, 158, 14, 1) 0%,
    rgba(236, 169, 34, 1) 100%
  );
  width: 200px;
  height: 200px;
  border-radius: 100%;
  margin: auto;
  display: grid;
  place-items: center;
  box-shadow: 0 9px rgb(163, 109, 9);
  animation-direction: alternate;
}
.scissors-center {
  background: white;
  width: 150px;
  height: 150px;
  border-radius: 100%;

  display: grid;
  place-items: center;
}

.scissors-center img {
  width: 60px;
}

@keyframes pulsateScissor {
  0% {
    box-shadow: 0 9px rgb(163, 109, 9), 0 0 5px 20px #a3751f9f,
      0 0 5px 40px #a3751f80, 0 0 5px 60px #a3751f5d;
  }
  100% {
    box-shadow: 0 9px rgb(163, 109, 9), 0 0 5px 60px #a3751f9f,
      0 0 5px 120px #a3751f80, 0 0 5px 180px #a3751f5d;
  }
}

@media only screen and (max-width: 500px) {
  .scissors {
    width: 100px;
    height: 100px;
  }
  .scissors-center {
    width: 75%;
    height: 75%;

    display: grid;
    place-items: center;
  }
  .scissors-center img {
    width: 30px;
  }
}

.rock {
  background: linear-gradient(
    90deg,
    rgba(220, 46, 78, 1) 0%,
    rgba(221, 64, 93, 1) 100%
  );
  box-shadow: 0 9px rgb(145, 30, 51);
  width: 200px;
  height: 200px;
  border-radius: 100%;
  margin: auto;
  display: grid;
  place-items: center;
  animation-direction: alternate;
}

.rock-center {
  background: white;
  width: 150px;
  height: 150px;
  border-radius: 100%;
  animation: none;
  display: grid;
  place-items: center;
}

.rock-center img {
  width: 60px;
}

@keyframes pulsateRock {
  0% {
    box-shadow: 0 9px rgb(145, 30, 51), 0 0 5px 20px #911e338a,
      0 0 5px 40px #911e3360, 0 0 5px 60px #911e333f;
  }
  100% {
    box-shadow: 0 9px rgb(145, 30, 51), 0 0 5px 60px #911e338a,
      0 0 5px 120px #911e3360, 0 0 5px 180px #911e333f;
  }
}
@media only screen and (max-width: 500px) {
  .rock {
    width: 100px;
    height: 100px;
  }
  .rock-center {
    width: 75%;
    height: 75%;

    display: grid;
    place-items: center;
  }
  .rock-center img {
    width: 30px;
  }
}

.choice-main {
  margin: auto;
  width: 500px;
  height: 550px;

  background-image: url(/rock-paper-scissors/static/media/bg-triangle.c0c30b56.svg);
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: 300px 300px;
  padding: 15px;
}
.firstRow {
  display: flex;
  justify-content: space-between;
  height: 250px;
}

.secondRow {
  display: flex;
  justify-content: center;
  height: 250px;
}

@media only screen and (max-width: 500px) {
  .choice-main {
    width: 300px;
    height: 300px;
    margin: auto;
    background-size: 180px 180px;
    margin-top: 20%;
  }
  .firstRow {
    height: 50%;
  }
  .secondRow {
    height: 50%;
  }
}

.game-main {
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  width: 55%;
  text-align: center;
  color: white;
}
.player-choice {
  width: 300px;
  line-height: 100px;
  font-size: 25px;
  font-weight: 700;
  position: relative;
}

.house-choice {
  width: 300px;
  line-height: 100px;
  font-size: 25px;
  font-weight: 700;
  animation-timing-function: linear;
  animation-duration: 1s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}
.house-choosing {
  width: 200px;
  line-height: 100px;
  font-size: 25px;
  font-weight: 700;
}
.house-choosing-symbol {
  opacity: 0.5;
  margin: auto;
  background: white;
  width: 200px;
  height: 200px;
  border-radius: 100%;
  display: grid;
  place-items: center;
  animation-name: loading;
  animation-timing-function: ease;
  animation-duration: 0.5s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

.display-winner {
  margin: auto;
  height: 200px;
  display: grid;
  place-items: center;
  font-size: 40px;
  font-weight: 700;
}

.play-again {
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-size: 20px;
  font-weight: 600;
  border: none;
  background-color: white;
  width: 220px;
  height: 50px;
  margin: 20px;
  border-radius: 10px;
  color: rgb(212, 69, 69);
}

@keyframes loading {
  0% {
    background-color: rgb(56, 56, 56);
  }
  10% {
    background-color: rgb(58, 58, 58);
  }
  20% {
    background-color: rgb(60, 60, 60);
  }
  30% {
    background-color: rgb(62, 62, 62);
  }
  40% {
    background-color: rgb(64, 64, 64);
  }
  50% {
    background-color: rgb(66, 66, 66);
  }
  60% {
    background-color: rgb(68, 68, 68);
  }
  70% {
    background-color: rgb(70, 70, 70);
  }
  80% {
    background-color: rgb(72, 72, 72);
  }
  100% {
    background-color: rgb(74, 74, 74);
  }
}
@media only screen and (max-width: 500px) {
  .game-main {
    width: 300px;
    height: 300px;
    margin: auto;
    background-size: 180px 180px;
    margin-top: 20%;
  }
  .player-choice {
    width: 150px;
    line-height: 60px;
    font-size: 15px;
  }
  .house-choice {
    width: 150px;
    line-height: 60px;
    font-size: 15px;
  }
  .play-again {
    font-size: 15px;
    width: 170px;
    height: 40px;
    color: hsl(229, 25%, 31%);
  }
  .display-winner {
    position: absolute;
    margin-top: 200px;
    height: 150px;
    font-size: 50px;
    z-index: 2;
  }
  .house-choosing {
    width: 150px;
    line-height: 60px;
    font-size: 15px;
    font-weight: 700;
  }
  .house-choosing-symbol {
    width: 100px;
    height: 100px;
  }
}

.modal {
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  height: 100%;
  width: 100%;
  transition: 1.1s ease-out;
  -webkit-filter: blur(0);
          filter: blur(0);
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  margin: auto;
  position: fixed;
  z-index: 3; /* Sit on top */
  left: 0;
  top: 0;
  display: flex;
}
.modal.off {
  opacity: 0;
  visibility: hidden;
  -webkit-filter: blur(8px);
          filter: blur(8px);
  transform: scale(0.33);
  box-shadow: 1rem 0 0 rgba(0, 0, 0, 0.2);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  border-radius: 10px;
}

.modal-content img {
  margin: auto;
  padding: 15px;
  padding-top: 25px;
  width: 300px;
}

.actions {
  display: flex;
  justify-content: space-between;
}

.actions button {
  border: none;
  background: none;
  font-family: 'Nunito', sans-serif;
  font-size: 25px;

  color: rgb(192, 192, 192);
}

.rules-text {
  font-size: 25px;
  font-weight: 700;
  color: #3b4363;
}

@media only screen and (max-width: 375px) {
  .modal {
    background-color: white;
    transition: none;
  }
  .modal-content {
    border: none;
  }
  .modal-content img {
    margin: auto;
    padding: 15px;
    padding-top: 25px;
    width: 250px;
  }
  .actions button {
    border: none;
    background: none;
    font-family: 'Nunito', sans-serif;
    font-size: 25px;
    position: absolute;
    color: rgb(192, 192, 192);
    margin-top: 400px;
  }
  .actions {
    display: flex;
    justify-content: center;
  }
}

.rulesButton-main {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.rules-button {
  border: none;
  background: none;
  width: 130px;
  height: 40px;
  font-weight: 600;
  border-style: solid;
  border-width: 2px;
  border-radius: 7px;
  border-color: #b4b4b4;
  margin-right: 30px;
  display: grid;
  place-items: center;
  cursor: pointer;
  color: rgb(221, 221, 221);
}

@media only screen and (max-width: 375px) {
  .rulesButton-main {
    justify-content: center;
  }
  .rules-button {
    margin: auto;
  }
}

